import Vue from 'vue';
import MaucashModel from './packages/maucash-model';
// tslint:disable-next-line: no-var-requires
const path = require('path');
const files = require.context('./packages', true, /index\.ts/);
console.log(files, '??');
console.log(files.keys());
files.keys().forEach((key) => {
  const name = path.basename(key, '.ts');
  // modules[name] = file(key).default || file(key)
  console.log(key, '==========', name, '========', files(key).default);
});

const components: { [propsName: string]: any } = {
  MaucashModel,
};

const install = (vue: typeof Vue): void => {
  // 安装全部的插件
  Object.keys(components).forEach((key) => {
    vue.component(key, components[key]);
  });
};

export default {
  install,
  ...components,
};
